// 
const currentTime = (format) => {
  let d = new Date();
  let year = d.getFullYear(); //
  let month = d.getMonth() + 1; // Date  (0 ~ 11)+1
  let day = d.getDay(); //
  let days = d.getDate(); //
  let hour = d.getHours(); //
  let minute = d.getMinutes(); //
  let second = d.getSeconds(); //

  if (month < 10) month = "0" + month;
  if (days < 10) days = "0" + days;
  if (hour < 10) hour = "0" + hour;
  if (minute < 10) minute = "0" + minute;
  if (second < 10) second = "0" + second;

  let week = new Array(
    "",
    "",
    "",
    "",
    "",
    "",
    ""
  );

  let ym = `${year}${month}${days}`; // 
  let yymm = `${year}-${month}`; // 
  let yymmhh = `${year}-${month}-${days}`;
  let time = hour + ":" + minute;
  var weekend = week[day];

  if (format == "YYYYMM") {
    return `${year}${month}`;
  }
  if (format == "YYYY-MM") {
    return `${year}-${month}`;
  }
  return {
    ym,
    time,
    yymm,
    weekend,
    yymmhh,
  };
}

/**
 * 
 * @param {String} time1 
 * @param {String} time2 
 * @return {String} 
 */
const timeDifference = (time1, time2) => {
  let dateBegin;
  let dateEnd;
  if (new Date(time1).getTime() > new Date(time2).getTime()) {
    dateEnd = new Date(time1).getTime();
    dateBegin = new Date(time2).getTime();
  } else {
    dateEnd = new Date(time2).getTime();
    dateBegin = new Date(time1).getTime();
  }
  let dateDiff = dateEnd - dateBegin; //
  //
  let dayDiff =
    Math.floor(dateDiff / (24 * 3600 * 1000)) != 0
      ? Math.floor(dateDiff / (24 * 3600 * 1000)) + ""
      : "";
  let leave1 = dateDiff % (24 * 3600 * 1000); //
  //
  let hours =
    Math.floor(leave1 / (3600 * 1000)) != 0
      ? Math.floor(leave1 / (3600 * 1000)) + ""
      : "";
  //
  let leave2 = leave1 % (3600 * 1000); //
  let minutes =
    Math.floor(leave2 / (60 * 1000)) != 0
      ? Math.floor(leave2 / (60 * 1000)) + ""
      : ""; //
  //
  let leave3 = leave2 % (60 * 1000); //
  let seconds =
    Math.round(leave3 / 1000) != 0 ? Math.round(leave3 / 1000) + "" : "";
  // console.log(' ' + dayDiff + hours + minutes + seconds)
  return dayDiff + hours + minutes + seconds;
}

/**
 * 
 * @param {String} time 
 * @return {String} value_description
 */
const getMonthBetween = (time, type = 1) => {
  const year = new Date(time).getFullYear();
  const month = new Date(time).getMonth();
  const firstDay = new Date(year, month, 1);
  const lastDay = new Date(year, month + 1, 0, 23, 59, 59);
  if (type === 1) {
    return {
      firstDay: formatDate({ date: firstDay }),
      lastDay: formatDate({ date: lastDay }),
    };
  }
  if (type === 2) {
    return { firstDay, lastDay };
  }
  if (type === 3) {
    return {
      firstDay: firstDay.getTime(),
      lastDay: lastDay.getTime(),
    };
  }
}

/**
 * 
 * @param {Number} timestamp 
 * @return {String} 
 */
const formatSecond = (timestamp) => {
	if (!timestamp) return '-'
	
	const formatTen = (num) => {
		return num < 10 ? `0${num}` : num
	}
	
	const d = parseInt(timestamp / (1000 * 60 * 60 * 24));
	const h = parseInt((timestamp % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
	const m = parseInt((timestamp % (1000 * 60 * 60)) / (1000 * 60));
	const s = ((timestamp % (1000 * 60)) / 1000).toFixed(0);
	const days = d > 0 ? d + "" : "";
	const hours = h > 0 ? formatTen(h) + "" : "";
	const minutes = m > 0 ? formatTen(m) + "" : "";
	const seconds = s > 0 ? formatTen(s) + "" : "";
	return `${days}${hours}${minutes}${seconds}`;
}

/**
 * 
 * @param {String|Number} date 
 * @param {String} format yyyy-MM-dd
 * @return {String} 
 */
const formatDate = (params) => {
  let { date, format } = params
  if (typeof date === "string") {
    date = date.replace("+08:00", "").replace("T", " ").replace("Z", "");
  }
  format = typeof format === "string" ? format : "YYYY-MM-DD HH:mm:ss";
	if (date) {
		date = new Date(date)
	}
	else {
		date = new Date()
	}
  var o = {
    "M+": date.getMonth() + 1, // 
    "D+": date.getDate(), // 
    "H+": date.getHours(), // 
    "m+": date.getMinutes(), // 
    "s+": date.getSeconds(), // 
    "q+": Math.floor((date.getMonth() + 3) / 3), // 
    S: date.getMilliseconds(), // 
  };
  if (/(Y+)/.test(format))
    format = format.replace(
      RegExp.$1,
      (date.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  for (var k in o)
    if (new RegExp("(" + k + ")").test(format))
      format = format.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  return format;
}

export {
	formatDate,
	formatSecond,
	getMonthBetween,
	timeDifference,
	currentTime
}
