import ConfigStore from '../utils/config-store';

const FileSystemManager = wx.getFileSystemManager()

/**
 *  fileId 
 * @param {String} fileId id
 * @return {String} 
 */
function getFileUrl(fileId) {
	return ConfigStore.downloadConfig.getUrlFun(fileId)
}

/**
 * 
 * @param {String} url 
 * @return {Promise} 
 */
function downloadFile(url) {
	return new Promise((resolve, reject) => {
		wx.downloadFile({
			url,
			header: ConfigStore.downloadConfig.header,
			timeout: ConfigStore.downloadConfig.timeout,
			filePath: ConfigStore.downloadConfig.filePath,
			success: (res)=> {
				if (res.tempFilePath.indexOf('.json') !== -1) {
					uni.showToast({
						title: '',
						icon: 'error'
					})
					reject('')
				}
				else {
					resolve(res.tempFilePath)
				}
			},
			fail: (err)=>{
				reject(err)
			}
		})
	})
}

/**
 * 
 * @param {String} fileId idurl
 * @return {Promise} 
 */
async function downloadToTemp(fileId) {
	uni.showLoading({
		title: '',
		mask: true
	})
	try{
		const { path } = await hasTempPath(fileId)
		uni.hideLoading()
		return path
	}catch(e){
		const url = getFileUrl(fileId)
		const path = await downloadFile(url)
		const fileManager = uni.getStorageSync('CONFIGUI_FILE_MANAGER') || {}
		fileManager[fileId] = {
			type: 'temp',
			path
		}
		uni.setStorageSync('CONFIGUI_FILE_MANAGER', fileManager)
		uni.hideLoading()
		return path
	}
}

/**
 * 
 * @param {String} fileId id
 */
function downloadToStorage(fileId) {
	uni.showLoading({
		title: '',
		mask: true
	})
	const fileManager = uni.getStorageSync('CONFIGUI_FILE_MANAGER') || {}
	const url = getFileUrl(fileId)
	downloadFile(url)
		.then(path => {
			const storagePath = FileSystemManager.saveFileSync(path)
			fileManager[fileId] = {
				path: storagePath,
				type: 'storage'
			}
			uni.setStorageSync('CONFIGUI_FILE_MANAGER', fileManager)
			uni.showToast({ title: '' })
		})
		.catch(e => {
			uni.hideLoading()
			console.warn(e)
		})
}

/**
 * 
 * @param {String} fileId id
 * @return {Promise}
 */
function hasTempPath(fileId) {
	const fileManager = uni.getStorageSync('CONFIGUI_FILE_MANAGER')

	return new Promise((resolve, reject) => {
		// 
		if (fileManager && fileManager[fileId]) {
			const info = fileManager[fileId]
			FileSystemManager.getFileInfo({
				filePath: info.path,
				success: () => {
					resolve(info)
				},
				fail: () => {
					// 
					delete fileManager[fileId]
					uni.setStorageSync('CONFIGUI_FILE_MANAGER', fileManager)
					reject(false)
				}
			})
		}
		else {
			reject(false)
		}
	})
}

/**
 * 
 * 
 * 
 * 
 * @param {String} fileId id
 */
function downloadHandler(fileId) {
	return hasTempPath(fileId)
		.then(info => {
			// type - storagetemp
			if (info.type === 'storage') {
				return uni.showToast({
					title: ''	,
					icon: 'none'
				})
			}
			else {
				const fileManager = uni.getStorageSync('CONFIGUI_FILE_MANAGER') || {}
				// 
				const storagePath = FileSystemManager.saveFileSync(info.path)
				fileManager[fileId] = {
					path: storagePath,
					type: 'storage'
				}
				uni.setStorageSync('CONFIGUI_FILE_MANAGER', fileManager)
				uni.showToast({ title: '' })
			}
		})
		.catch(() => {
			// 
			downloadToStorage(fileId)
		})
}

/**
 * 
 * @param {Object} data
 * @param {String} data.path  ()
 * @return {Promise} 
 */
function handleUpload(data = {}) {
	if (!data.path) return Promise.reject('');

	const { uploadConfig } = ConfigStore

	if (!uploadConfig.url) throw new Error('');

  return new Promise((resolve, reject) => {
    uni.uploadFile({
      url: uploadConfig.url,
      filePath: data.path,
			name: 'file',
      header: {
        'Content-Type': 'multipart/form-data',
        ...uploadConfig.header
      },
      success: (uploadFileRes) => {
        const res = JSON.parse(uploadFileRes.data)
        uploadConfig.success({ res, resolve, reject })
      }
    })
  })
}

/**
 * KBMBGBTB
 * @param {Number} size 
 * @return {String} 
 */
function fileSizeFormatter(size) {
  if (!size && size !== 0) return '';
  if (size < Math.pow(1024, 2))
    return (size / Math.pow(1024, 1)).toFixed(2) + ' KB';
  if (size < Math.pow(1024, 3))
    return (size / Math.pow(1024, 2)).toFixed(2) + ' MB';
  if (size < Math.pow(1024, 4))
    return (size / Math.pow(1024, 3)).toFixed(2) + ' GB';
  return (size / Math.pow(1024, 4)).toFixed(2) + ' TB';
}

/**
 * 
 * @param {String} fileId idurl
 * @param {String} fileName 
 */
const shareFile = async (fileId, fileName = '') => {
	const tempFilePath = await downloadToTemp(fileId)
	await wx.shareFileMessage({
		filePath: tempFilePath, // 
		fileName
	})
}

/**
 *     
 * @param {String} path  ()
 * @return {Promise}
 */
const getFileInfo = (path, digestAlgorithm = 'md5') => {
	return new Promise((resolve, reject) => {
		FileSystemManager.getFileInfo({
			filePath: path,
			digestAlgorithm,
			success(res) {
				resolve(res)
			},
			fail(err) {
				reject(err)
			}
		})
	})
}

/**
 * 
 * 
 * @param {string} filename - 
 * @returns {string} 
 * 
 * 
 * - \.([^.]+)?: .
 * - $: 
 * 
 * 
 */
const getFileExtension = (filename) => {
  // 
  const regex = /(?:\.([^.]+))?$/;
  const match = filename.match(regex);
  return match ? match[1] : '';
}

export {
	downloadToTemp,
	downloadHandler,
	handleUpload,
	fileSizeFormatter,
	getFileUrl,
	shareFile,
	getFileInfo,
  getFileExtension
}
